import { mapState } from "vuex";
import { InfiniteScroll } from "mint-ui";
import vLoadmore from "@/components/v-loadmore";
import vNodata from "@/components/v-nodata";
import vImglist from "@/components/v-imglist";
import vHeader from "@/components/v-header";
export default {
  data() {
    return {
      avatar: require('../../assets/images/avatar.png'),
      id: this.getUrlParam("id"),
      evasPageNo: 1,
      evasPageSize: 10,
      moreEvas: false,
      noEvas: false,
      loading: true,
      evasList: [],
      goBack:''
    };
  },
  components: {
    "v-loadmore": vLoadmore,
    "v-nodata": vNodata,
    "v-imglist": vImglist,
    vHeader
  },
  computed: {
    ...mapState(["token"])
  },
  beforeCreate() {
    document.title = "商品评价";
  },
  created() {
    this.getEvasList(this.id, "first");
  },
  methods: {
    // 加载更多评价
    loadMore() {
      this.loading = true;
      this.evasPageNo += 1;
      this.getEvasList(this.id);
    },
    // 获取评价列表
    getEvasList(id, first) {
      this.$axios.post(this.api.goodsCommentList,
          JSON.stringify({
          goodsId: id,
          limit: 80,
          page: 1
        }), {
          headers: {"content-type": "application/json"}
        })
        .then(res => {
          const resData = res.data;
          if (resData.code !== 1) {
            if (first) {
              this.noEvas = true;
            } else {
              this.moreEvas = true;
              this.loading = false;
            }
            return;
          }
          let objData = resData.content;
          if (objData.length === 0) {
            if (first) {
              this.noEvas = true;
            }
            this.loading = true;
            return;
          }
          // 重组下数据
          objData.list.forEach(val => {
            this.evasList.push({
              avatar: val.photoAvatarUrl||this.avatar,
              name: this.formatPhone(val.userName) || "匿名",
              star: val.starNum,
              content: val.context,
              date: val.addTime,
              title: val.goodsName,
              imgList: val.photoURLs.split(',')
            });
          });
          if (pageCount <= this.evasPageNo) {
            this.loading = true;
            this.moreEvas = false;
          } else {
            this.loading = false;
            this.moreEvas = true;
          }
        })
        .catch(res => {
         /* if (first) {
            this.noEvas = true;
          } else {
            this.loading = false;
          }*/
        });
    }
  }
};



